@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  margin: 0;
  padding: 0;
  
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --primary: 237, 73, 86;
  --color: 44, 62, 80;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main-header{
    
    display: flex;
    align-items: center;
    width: 100%;
    height: 75px;
    position:fixed;
    top:0;
    left: 0;
    z-index: 5;
    padding: 0 1rem;
    background: #42242e;
}

main {
  margin-top: 6rem;
}

@media (min-width: 768px) {
    .main-header {
      justify-content: space-between;
    }
  }
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.75);
    z-index: 10;
  }
.side-drawer {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 100;
    height: 100vh;
    width: 50%;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  }
.navigation__title{
    color:#ffffff;
   
}

.navigation__title a{
  text-decoration: none;
  color:#ffffff;
}

.navigation__menu-btn{
    width: 3rem;
    height: 3rem;
    background: transparent;
    border: none;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-right: 2rem;
    cursor: pointer;
}
.navigation__menu-btn span {
    display: block;
    width: 3rem;
    height: 2.5px;
    background: white;
  }

  .navigation__header-nav {
    display: none;
    
  }

  .main-navigation__drawer-nav {
    height: 100%;
    margin-top: 23px;
  }

.nav-links{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100;
    padding-right: 1rem;
    
}
.nav-links li{
    margin: 1rem;
}

.nav-links a{
    padding: 8px;
    text-decoration: none;
    color: black;
    font-weight: 500;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #ffffff;
  font-weight: bold;
  color: #070707;
}

.nav-links li:hover{
  transition: all .1s ease-in-out;
  transform: scale(1.07);
  
  
}



@media (min-width: 768px) {
    .navigation__menu-btn {
      display: none;
    }
  
    .navigation__header-nav {
      display: block;
    }
    
  }
@media (min-width: 768px) {
    .nav-links {
      flex-direction: row;
    }
  
    .nav-links li {
      margin: 0 0.5rem;
  
    }
  
    .nav-links a {
      color: white;
      text-decoration: none;
      
    }
    

}
.container{
  display: flex;
  align-items: center;
  justify-content: center;
}

.aboutme{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex: 1 1 90%;

 
}

.aboutme__image img{
  width: 100%;
  max-width: 400px;
  height: auto;
  margin-bottom: 15px;
}

.aboutme__info{
 width: 500px;
 padding: 10px;
}
.aboutme__info p{
  padding-top:8px;
  
}

.aboutme__links{
  padding-left: 10px;
  font-size: 2rem;

}
.aboutme__links a:hover{
  transition: all .1s ease-in-out;
  transform: scale(1.05);
}

.aboutme__links .links{
  font-size: 2rem;
  color: black;
}
.aboutme__links .links:hover{
  transition: all .1s ease-in-out;
  transform: scale(1.09);
}
.portfolio{
    display: flex;
    flex-wrap: wrap;
    padding: 0 4px;
    justify-content: center;
    
}
.project{
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1 1 30%;
    
    padding: 10px 5px;
    
    border-radius: 5px;
    margin:5px;
  
}

.project__image img{
  max-width: 100%;
}
.project__title{
    
    height: 175px;

}

.project__specification{
    padding: 10px 9px;
    border-bottom: 1px solid lightgrey;
}

.project__links{
    padding-top: 6px;
    width: 100%;
    
}

.project__links button{
  margin-left: 6px;
  padding: 6px 18px;
  border:none;
  width: 48%;

  

}

.project__links button:hover{
  color:#000000;
  background-color: #ffffff;
}



@media (max-width: 800px) {
    .project {
     flex: 1 1 46%;
    }
  }

  
  @media (max-width: 375px) {
    .project {
      flex-direction: column;
      
   
    }
    
  }
  
 
.contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    padding:35px;
    
}

.contact > h1{
    font-weight: normal;
    font-size: 1.8rem;
    margin-bottom: 10px;
}

.contact__container{
    width: 300px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border:1px solid lightgray;
    border-radius: 3px;
    padding:10px;

}

.contact__container h5{
    margin-bottom: 8px;
}

.contact__container form input{
    margin: 7px 0;
    width:250px;
    padding: 14px 20px;
    border: 0;
    border-radius: 5px;
    border: 2px solid rgb(var(--color));
    transition: all .25s ease;
    opacity: .2;
    background: #c3c8cf;
}
.contact__container form input:hover{
    opacity: 1;
}

.contact__container textarea{
    border: 2px solid rgb(var(--color));
    transition: all .25s ease;
    opacity: .2;
    background: #c3c8cf;
    width: 290px;
}

.contact__container form textarea:hover{
    opacity: 1;
}

.contact__container p{
    margin-top: 5px;
    text-align: center;
}

.contact__container button{
    border:0; 
    padding:14px 20px;
    color: #ffffff;
    width: 290px;
    cursor: pointer;
    font-size: 1,1rem;
    background: rgb(var(--primary));
    transition: all .25s ease;
    margin-top: 15px;
}

.contact__container button:hover{
    transform: translate(0, -10px);
    box-shadow: 0 10px 30px 0 rgb(var(--primary), .4);
}

